<template>
<div id="demo-button">
  <weui-button type="primary"> Normal</weui-button>
  <weui-button type="primary" :loading="true"> Loading</weui-button>
  <weui-button type="primary" :disabled="true"> Disabled</weui-button>
  
  <weui-button type="default"> Normal</weui-button>
  <weui-button type="default" :loading="true"> Loading</weui-button>
  <weui-button type="default" :disabled="true"> Disabled</weui-button>
  
  <weui-button type="warn"> Normal</weui-button>
  <weui-button type="warn" :loading="true"> Loading</weui-button>
  <weui-button type="warn" :disabled="true"> Disabled</weui-button>
  
  <div class="button-padding">
    <weui-button type="default" :plain="true"></weui-button>
    <weui-button type="default" :plain="true" :loading="true"></weui-button>
    <weui-button type="default" :plain="true" :disabled="true"></weui-button>
    
    <weui-button type="primary" :plain="true"></weui-button>
    <weui-button type="primary" :plain="true" :loading="true"></weui-button>
    <weui-button type="primary" :plain="true" :disabled="true"></weui-button>
    
    <weui-button type="default" :mini="true"></weui-button>
    <weui-button type="primary" :mini="true"></weui-button>
    <weui-button type="warn" :mini="true"></weui-button>
    
    <weui-button type="default" :plain="true" :mini="true"></weui-button>
    <weui-button type="primary" :plain="true" :mini="true"></weui-button>
  </div>
</div>
</template>

<script>
export default {
  name: 'demo-button',
}
</script>

<style type="text/css" lang="scss" scope>
#demo-button {
  .button-padding {
    padding: 15px 0;
    margin: 0 auto;
    width: 60%;
  }
}
</style>


