<template>
<div id="demo-button">
  <weui-button @click.native="show.ios1 = true">iOS Dialog</weui-button>
  <weui-button @click.native="show.ios2 = true">iOS Dialog</weui-button>
  <weui-button @click.native="show.android1 = true">Android Dialog</weui-button>
  <weui-button @click.native="show.android2 = true">Android Dialog</weui-button>
  
  <weui-dialog :show.sync="show.ios1">
    <weui-dialog-header>
      <weui-dialog-title></weui-dialog-title>
    </weui-dialog-header>
    <weui-dialog-body></weui-dialog-body>
    <weui-dialog-footer>
      <weui-dialog-btn type="default" @click.native="show.ios1 = false"></weui-dialog-btn>
      <weui-dialog-btn type="primary" @click.native="show.ios1 = false"></weui-dialog-btn>
    </weui-dialog-footer>
  </weui-dialog>
  
  <weui-dialog :show.sync="show.ios2">
    <weui-dialog-body></weui-dialog-body>
    <weui-dialog-footer>
      <weui-dialog-btn type="primary" @click.native="show.ios2 = false"></weui-dialog-btn>
    </weui-dialog-footer>
  </weui-dialog>
  
  <weui-dialog :show.sync="show.android1" :android="true">
    <weui-dialog-header>
      <weui-dialog-title></weui-dialog-title>
    </weui-dialog-header>
    <weui-dialog-body></weui-dialog-body>
    <weui-dialog-footer>
      <weui-dialog-btn type="default" @click.native="show.android1 = false"></weui-dialog-btn>
      <weui-dialog-btn type="primary" @click.native="show.android1 = false"></weui-dialog-btn>
    </weui-dialog-footer>
  </weui-dialog>
  
  <weui-dialog :show.sync="show.android2" :android="true">
    <weui-dialog-body></weui-dialog-body>
    <weui-dialog-footer>
      <weui-dialog-btn type="default" @click.native="show.android2 = false"></weui-dialog-btn>
      <weui-dialog-btn type="primary" @click.native="show.android2 = false"></weui-dialog-btn>
    </weui-dialog-footer>
  </weui-dialog>
</div>
</template>

<script>
export default {
  name: 'demo-dialog',
  data () {
    return {
      show: {
        ios1: false,
        ios2: false,
        android1: false,
        android2: false,
      }
    }
  },
}
</script>


