<template>
<div>
  <weui-cells-title> - {{ weui_radio }}</weui-cells-title>
  <weui-cells type="radio">
    <weui-input-radio name="radio1" value="1" v-model="weui_radio">cell standard. value: 1</weui-input-radio>
    <weui-input-radio name="radio1" value="2" v-model="weui_radio" checked>cell standard. value: 2</weui-input-radio>
    
    <weui-cell href="javascript:void(0);" :link="true" :access="false">
      <weui-cell-body></weui-cell-body>
    </weui-cell>
  </weui-cells>

  <weui-cells-title> -  {{ weui_checkbox }}</weui-cells-title>
  
  <weui-cells type="checkbox">
    <weui-input-checkbox name="checkbox1" value="vue" v-model="weui_checkbox">vue</weui-input-checkbox>
    <weui-input-checkbox name="checkbox1" value="weui" v-model="weui_checkbox">weui</weui-input-checkbox>
    <weui-input-checkbox name="checkbox1" value="components" v-model="weui_checkbox">components</weui-input-checkbox>
    <weui-input-checkbox name="checkbox1" value="vue-weui-components" v-model="weui_checkbox">vue-weui-components</weui-input-checkbox>
    
    <weui-cell href="javascript:void(0);" :link="true" :access="false">
      <weui-cell-body></weui-cell-body>
    </weui-cell>
  </weui-cells>
  
  <weui-cells-title></weui-cells-title>
  <weui-cells type="form">
    
    <weui-input type="number" pattern="[0-9]*" placeholder="qq" v-model="weui_form.qq">qq</weui-input>
    
    <weui-input type="tel" placeholder="" :vcode="true" v-model="weui_form.mobile">
      
      <a slot="vcode" href="javascript:;" class="weui-vcode-btn"></a>
    </weui-input>
    
    <weui-input type="date" v-model="weui_form.date"></weui-input>
    <weui-input type="datetime-local" placeholder="" v-model="weui_form.datetime"></weui-input>
    
    <weui-input type="number" placeholder="" :vcode="true" v-model="weui_form.vcode">
      
      <img slot="vcode" class="weui-vcode-img" src="static/img/vcode.jpg" />
    </weui-input>
    
  </weui-cells>
  
  <weui-cells-tips></weui-cells-tips>
  
  <weui-cells-title></weui-cells-title>
  <weui-cells type="form">
    <weui-input type="number" pattern="[0-9]*" placeholder="" :warn="true" v-model="weui_form.cardno"></weui-input>
  </weui-cells>

  <weui-cells-title> - {{ weui_switch }}</weui-cells-title>
  <weui-cells type="form">
    <weui-input-switch v-model="weui_switch"></weui-input-switch>
  </weui-cells>

  <weui-cells-title></weui-cells-title>
  <weui-cells>
    <weui-input type="text" placeholder="" v-model="weui_text" :label="false"></weui-input>
  </weui-cells>

  <weui-cells-title></weui-cells-title>
  <weui-cells type="form">
    <weui-textarea placeholder="" rows="3" :limit="200" v-model="weui_textarea"></weui-textarea>
  </weui-cells>

  <weui-cells-title> - {{ weui_select.country_code }} - {{ weui_select.mobile }}</weui-cells-title>
  <weui-cells>
    <weui-select v-model="weui_select.country_code" place="before">
      <option value="86">+86</option>
      <option value="80">+80</option>
      <option value="84">+84</option>
      <option value="87">+87</option>
      <input slot="after" class="weui-input" type="number" pattern="[0-9]*" placeholder="" v-model="weui_select.mobile">
    </weui-select>
  </weui-cells>

  <weui-cells-title> - 1{{ weui_select.account }} - 2{{ weui_select.country }}</weui-cells-title>
  <weui-cells>
    
    <weui-select v-model="weui_select.account">
      <option value="wechat"></option>
      <option value="qq">QQ</option>
      <option value="email">Email</option>
    </weui-select>
    
    <weui-select v-model="weui_select.country" place="after">
      <label slot="before" class="weui-label">/</label>
      <option value="CN"></option>
      <option value="US"></option>
      <option value="UK"></option>
    </weui-select>
    
  </weui-cells>
  
  <weui-input-agree v-model="weui_agree">
    <a href="javascript:void(0);"></a> - {{ weui_agree }}
  </weui-input-agree>
    
  <weui-button-area>
    <weui-button type="primary"></weui-button>
  </weui-button-area>

</div>
</template>

<script>
export default {
  name: 'demo-input',
  data () {
    return {
      weui_radio: 2,
      weui_checkbox: ['vue-weui-components'],
      weui_switch: true,
      weui_form: {
        qq: null,
        mobile: null,
        date: null,
        datetime: null,
        vcode: null,
        cardno: null,
      },
      weui_text: null,
      weui_textarea: null,
      weui_select: {
        country_code: '86',
        mobile: null,
        account: 'wechat',
        country: 'CN',
      },
      weui_agree: false,
    }
  }
}
</script>


